.entrypage{
    width: 100vw;
    height: 100vh;
    background-color: pink;
    display: flex;
    justify-content: center;
    align-items: center;
}
.entrycontent{
    display: flex;
    flex-direction: column;
}
.entrycontent>input{
    padding: 10px;
    border-radius: 10px;
    border: none;
    margin: 5px;
}
.entrycontent>.link{
    text-align: center;
    text-decoration: none;
    color: black;
    font-weight: 600;
    font-size: larger;
    padding:8px ;
    margin: 5px;
    border-radius: 10px;
    background-color: orange;
    border: none;
    cursor: pointer;
}
.entrycontent>.link:hover{
    background-color: rgb(44, 179, 116);
    color: antiquewhite;
    transition: background-color .4s ease-in-out;
}
.entrypage>p{
    color: rgb(165, 42, 81);
    position: fixed;
    top: 0;
    text-decoration: underline 4px solid rgb(193, 130, 62);
}
.entrypage>p>:nth-child(1){
    font-size: 100px;
}
.entrypage>p>:nth-child(2){
    font-size: 80px;
}
.entrypage>p>:nth-child(3){
    font-size: 60px;
}