.result{
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: #219849;
    display: flex;
    justify-content: center;
    align-items: center;
}
.resultcong>p{
    color: aliceblue;
    text-align: center;
    width: 70vw;
    height: 10vh;
    font-size: 40px;
}
.resultcong>p>span{
    color: #0a1933;
    font-weight: 600;
    text-decoration: underline;
}
.plag{
    font-size: larger;
    width: 10vw;
    background-color: #2da79c;
    cursor: pointer;
    border-radius: 30px;
    margin-left: 30vw;
    height: 7vh;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    /* border: 2px solid black; */
    transition: background-color .4s ease-in;
}
.plag:hover{
    background-color: #dbba45;
}

@media only screen and (max-width:1200px) {
    .plag{
        width: 30vw;
        margin-left: 20vw;
    }
}