.gamepages{
    background-image: url("./../../public/images/KBC.jpg");
    width: 100vw;
    animation: anime 6s linear infinite;
    height: 100vh;
   
    background-size: cover;
    border: 3px solid black;
    background-color: pink;
    display: flex;
}
.navbar{
    /* background-color: #e4da47; */
    background-image: linear-gradient(to right,rgb(32, 80, 46),rgb(117, 110, 75),rgb(101, 46, 210));
    position: fixed;
    top: 0;
    width: 80vw;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    height: 7vh;
    /* background-color: #fff; */
}
.navbar>span{
    margin-left: 40px;
    font-size: 30px;
    font-weight: 600;
    color: #f2f5f5;
}
.navbar>span>span{
    color: #f87858;
    text-decoration:underline ;
}
.game{
    background-repeat: repeat;
    flex: 0.8;
    /* border: 1px solid black; */
    background-position: -50px;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: anime 6s linear infinite;
}
@keyframes anime {
    0%{
        background-position: 0 0;
    }
    25%{
        background-position:+15px;
    }
    50%{
        background-position: -20px;
    }
    75%{
        background-position:+10px;
    }
    100%{
        background-position:0 0;
    }
    /* 60%{
        background-position:55px;
    }
    80%{
        background-position:0 0;
    } */
    
}
.status{
    /* padding-top: 30px; */
    flex:0.2;
    /* border: 1px solid black; */
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    /* box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset; */
    background-color: #b49b71;
    /* border-radius: 30px; */
}
.clock{
    display: flex;
    background-color: #fff;
    width: 40px;
    height: 40px;
    border: 1px solid black;
    border-radius: 50%;
}
.clock>p{
    font-size: larger;
    font-weight: 800;
    margin: auto;
}
.status>span{
    border-radius: 30px;
    transition: background-color .4s ease-in;
    /* padding: 21px; */
    cursor: pointer;
    font-size: x-large;
    padding-top: 6px;
    text-align: center;
    display: block;
    width: 100%;
    height: 6vh;
    border-bottom: 2px solid black;
}


.status>span:active{
    background-color: #b142e0;
    color: aliceblue;
}
.gamecontent{
   /* margin-left: 10px; */
}
.gamecontent>.question{
    background-color: #3a8a7c;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px;
    width: 70vw;
    height: 10vh;
    border: 2px solid black;
}
.gamecontent>.question>p{
    /* margin-left: 20px; */
    font-size: 20px;
    font-weight: 600;
}
.gamecontent>.option{
    display: grid;
    grid-template-columns:repeat(2,40vw);
    /* margin: 40px; */
    width: 70vw;
    height: max-content;
    /* border: 2px solid black//; */
}
.gamecontent>.option>li{
    color:rgb(34, 29, 88);
    cursor: pointer;
    padding: 10px;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    background-color: #e6ebed;
    border-radius: 10px;
    /* margin: 8px; */
    list-style: none;
    width: 25vw;
    height: 5vh;
    border: 2px solid black;
}
.gamecontent>.option>li:hover{
    background-color: #c0b21e;
    transition: background-color .5s ease-in;
}
.gamecontent>.option>li>span{
    font-weight: 800;
    margin-right: 1em;
}
.green{
    background-color:green;
}


@media only screen and (max-width:700px){
    .game{
        background-size:contain;
        background-repeat: repeat;
    }
    .navbar{
        width: 78.8vw;
    }
    .gamecontent>.option>li{
        
        width: 25vw;
        height: 5vh;
        
    }
    .gamecontent>.option{
        
        grid-template-columns:repeat(2,35vw);
        
    }
    .game{
        flex: 0.6;
    }
    .status{
        flex:0.4;
    }
    .status>span{
        /* padding: 21px; */
        font-size: 13px;
        
    }
    .status>p{
        font-size: 13px;
    }
    .gamecontent>.option>li>span{
        font-weight: 700;
        margin-right: .3em;
    }
    
}